<template>
  <div class="home">
    <v-header class="header"></v-header>
    <div class="g-container">
      <v-navigate class="navigate"></v-navigate>
      <div class="content-wrapper" :class="{collapseMenuMargin: !showMenu}">
          <router-view></router-view>
      </div>

    </div>
    

  </div>
</template>
<script>
import Navigate from "../layout/Navigate.vue";
import Header from "../layout/Header.vue";
export default {
  name: "Home",
  components: {
    vNavigate: Navigate,
    vHeader: Header
  },
  data() {
    return {};
  },
  computed: {
    showMenu () {
      return this.$store.state.manage.showMenuList
    }
  },
  created () {
    // this.$router.push('/creditsearch');
  }
};
</script>
<style lang="scss">
@import "../../static/CSS/variable.scss";
.g-container {
  position: absolute;
  top: $navigateHeight;
  left: 0;
  right: 0;
  bottom: 0;
}
.navigate {
  position: absolute;
  top: 0;
  left: 0;
  width: $navigateWidth;
  min-height: 100%;
}
.content-wrapper {
  position: relative;
  height: 100%;
  margin-left: 240px;
  overflow-x: auto;
  overflow-y: auto;
}
.collapseMenuMargin {
  margin-left: 64px;
}
</style>




