<template>
  <div class="credit-search">
    <!-- <el-button @click="go2Test" type="primary">测试页面</el-button> -->
    <div :class="{ listCreditSearch : showResultList }">
      <div
        v-if="!showResultList" 
        class="title-large title-credit-search">
        专注小微企业征信
      </div>
      <div class="btn-tab btn-credit-search">
        <el-button 
          @click="checkQuery(true)" 
          :class="{ isActive : true}"
          type="text" >
          公司名称
        </el-button>
        <!-- <el-button 
          @click="checkQuery(false)" 
          :class="{ isActive : !selectCompanyName}"
          type="text">
          统一社会信用代码
        </el-button> -->
      </div>
      <div class="search-container">
        <el-input 
          class="search-input" 
          v-model="input" 
          :placeholder="placeholderModel">
        </el-input>
        <el-button 
          @click="searchData"
          class="search-btn" 
          type="primary">
          立即搜索
        </el-button>
      </div>      
    </div>
    <div v-if="showResultList" class="list-container">
      <div v-if="showLoading == 'yes'">
        <v-loading></v-loading>
      </div>
      <div v-else>
        <div class="search-title">为您找到 <span>{{ Paging.TotalRecords | returnZero }}</span> 家符合条件的企业</div>
        <div v-for="(searchItem, index) in searchList" :key="index"
          class="list-item">
          <div class="item-title">{{ searchItem.Name }}</div>
          <div class="button-container">
            <el-button @click="preViewPaper(searchItem.Name)">预览</el-button>
            <el-button @click="downloadPaper(searchItem.Name)" type="primary">下载</el-button>
          </div>
          <div class="item-content">
            <div>
              <span>法人代表: {{ searchItem.OperName | checkVariable}}</span>            
              <span>成立时间: {{ searchItem.StartDate | checkVariable | formatTimeStamp }}</span>
              
            </div>
            <div>
              <span>经营状况: {{ searchItem.Status | checkVariable }}</span>
            </div>
            <div>
              <span>统一信用代码: {{ searchItem.CreditCode | checkVariable }} </span>
            </div>
          </div>
        </div>
      </div>
    </div>    
  </div>
</template>
<script>
import {mapMutations} from 'vuex'
import Loading from './Loading.vue'
export default {
  name: "CreaditSearch",
  components: {
    'vLoading': Loading
  },
  data() {
    return {
      input: "",
      showResultListHandler: false,
      selectCompanyName: true,       // 为 true 时，按公司名查询，为 false 时，按信用代码查询
      Paging: '',                    //返回结果中，与分页相关数据
      searchList: '',                 //返回结果中，查询列表

      showLoading: 'no',
      showResultList: false
    };
  },
  computed: {
    // showResultList: {
    //   get(value) {
    //     const vm = this;
    //     if (this.input === "") {
    //       return false;
    //     } else if (vm.showResultListHandler === true) {
    //       return true;
    //     } else {
    //       return false;
    //     }
    //   }
    // },
    placeholderModel() {
      const vm = this;
      if ( vm.selectCompanyName ) {
        return "请输入公司名称关键字  如: 百融金服"
      }else {
        return "请输入统一社会信用代码"
      }
    }
  },
  methods: { 
    ...mapMutations([
      
    ]),   
    preViewPaper (value) {
      const vm = this
      window.open('creditpaper.html?download=no&searchKey=' + value, '_blank')
    },
    downloadPaper (value) {
      const vm = this
      window.open('creditpaper.html?download=yes&searchKey=' + value, '_blank')
    },
    checkQuery(status) {
      const vm = this;
      vm.selectCompanyName = status;
    },
    doQueryData (resolve) {
      const vm = this;
      if (vm.input !== "") {
        vm.showResultListHandler = true;
      }
      vm.ajax(
        vm.apiHost + 'bizKeySearch',
        {
          search_key: vm.input
        },
        function(res) {
          console.log('creditSearch res data is : ', res)
          vm.Paging = res.Paging
          vm.searchList = res.Result
        }, 
        function () {
          resolve()
        }, 'get'
      )

    },
    searchData() {
      const vm = this

      let promise = new Promise(function(resolve, reject) {
        vm.showLoading = 'yes'
        vm.doQueryData(resolve)
      })

      promise.then(function(value) {
        if (vm.searchList.length == 0) {
          vm.showResultList = false
        }else {
          vm.showResultList = true
        }
        vm.showLoading = 'no'

      })
      
    }
   
  },
  created () {
    const vm = this
    document.onkeydown = function (e) {
      const ev = ( typeof event != 'undefined' ) ? window.event : e
      if ( ev.keyCode == 13) {
        vm.searchData()
      }
    }
  }
};
</script>
<style lang="scss" scoped>
.credit-search {
  position: relative;
  // width: inherit;
  height: auto;
  min-height: 100%;

  background: url("../assets/background-content.png") repeat;

  overflow-x: auto;
  // background-attachment: fixed;
}
.title-credit-search {
  padding-top: 114px;
}
.btn-credit-search {
  padding-top: 57px;
  margin: 0 auto 25px auto;
}
.btn-tab button {
  color: white;
  font-size: 16px;
  line-height: 22px;
  padding: 5px 10px;

  &:hover {
    color: black;
    background: white;
  }

  + button {
    margin-left: 50px;
  }
}
.isActive {
  color: black !important;
  background: white;
}
.search-input {
  width: 60%;
  min-width: 500px;
  height: 50px;
}
.search-btn {
  width: 13%;
  min-width: 102px;
  max-width: 120px;
  height: 50px;
}

// 显示搜索列表时的样式
.listCreditSearch {
  height: 250px;
}
.listTitle {
  display: none;
}
.list-container {
  width: 95%;
  min-width: 650px;
  height: auto;
  margin: 0 auto 20px auto;
  padding: 20px;
  background-color: white;
}
.search-title {
  font-size: 14px;
  line-height: 20px;
  color: #666666;
  text-align: left;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  > span {
    color: #c02e34;
  }
}
.list-item {
  padding: 18px 0 20px 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.item-title {
  font-size: 16px;
  text-align: left;
  color: #151515;
  font-weight: bold;
  line-height: 22px;
}
.item-content {
  padding-top: 16px;
  font-size: 14px;
  text-align: left;
  line-height: 20px;
  color: #666666;

  div + div {
    margin-top: 6px;
  }
  div > span + span {
    margin-left: 20px;
  }
}
.button-container {
  float: right;
  position: relative;
  width: 250px;
  top: 14px;

  .el-button {
    padding: 12px 30px;
  }
}
</style>





