import Vue from 'vue'
import Router from 'vue-router'
// import CreditPaperContainer from '@/layout/CreditPaperContainer'
import Home from '@/layout/Home'
import CreaditSearch from '@/components/CreditSearch'

Vue.use(Router)

export default new Router({ 
  routes: [    
    {
      path: '',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/',
          name: 'CreaditSearch',
          component: CreaditSearch
        },
        // {
        //   path: 'creditpaper',
        //   name: 'CreditPaperContainer',
        //   component: CreditPaperContainer
        // },        

      ]
    },    
    // {
    //   path: '/test',
    //   name: 'CreditPaperContainer',
    //   component: CreditPaperContainer
    // },
    
  ]
})  



