<template>
  <div>
    <el-menu 
      default-active="/" 
      class="el-menu-vertical-demo" 
      :collapse="!showMenu" router>
      <el-menu-item index="/" route>
        <i class="el-icon-document"></i>
        <span slot="title">企业征信报告</span>
      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: "",
  data() {
    return {
      collapse: false,
    }
  },
  computed: {
    showMenu () {
      console.log('Navigate showMenuList : ' + this.$store.state.manage.showMenuList);
      return this.$store.state.manage.showMenuList
    }
  },
   methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    },
    created () {
      
    }
}
</script>
<style lang="scss" scoped>
.logo-creditpaper {
  width: 15px;
  height: 20px;
}
img + span {
  font-size: 16px;
  color: #c02e34;
  line-height: 22px;
  margin-left: 10px;
}

</style>




