<template>
  <div class="header">
    <div class="title" :class="{collapseMenuWidth: !showMenu}">
      <img src="../assets/logo.png" alt="logo" class="title-img">
      <div v-if="showMenu" class="title-content test">
        企业征信平台
      </div>      
    </div>
    <div class="header-content">
      <div class="menu-icon">
        <img v-if="showMenu" @click="collapseMenu" src="../assets/menu-icon-collapse.png" alt="菜单收起">
        <svg v-if="!showMenu" @click="openMenu" class="icon" aria-hidden="true">
          <use xlink:href="#icon-arrow-straight"></use>
        </svg>
      </div>

    </div>

  </div>
</template>
<script>
import { mapMutations } from 'vuex'
export default {
  name: "",
  data() {
    return {};
  },
  computed: {
    showMenu () {
      return this.$store.state.manage.showMenuList
    }
  },
  methods: {
    ...mapMutations ([
      'updateShowMenuList'
    ]),
    collapseMenu () {
      // this.$store.commit('updateShowMenuList', false)
      this.updateShowMenuList(false)

      console.log("collapseMenu showMenuList : " + this.$store.state.manage.showMenuList);
    },
    openMenu () {
      // this.$store.commit('updateShowMenuList', true)
       this.updateShowMenuList(true)
      console.log("openMenu showMenuList : " + this.$store.state.manage.showMenuList);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../../static/CSS/variable.scss";
.header {
  height: $navigateHeight;
  background: #373d47;
}

.title {
  float: left;
  // width: 240px;
  width: $navigateWidth;
  // min-width: $navigateMinWidth;
  height: $navigateHeight;
  padding-top: 13px;
  background: #c02e34;

  .title-content {
    display: inline-block;
    vertical-align: middle;

    font-size: 18px;
    color: white;
    line-height: 25px;
    text-align: center;
  }
  .title-img {
    display: inline-block;
    vertical-align: middle;
    // margin-right: 10px;
    width: 38px;
    height: 38px;

    + div {
      margin-left: 10px;
    }
  }
}
// 导航栏收起时，标题栏的宽度减少
.collapseMenuWidth {
  width: 64px;
}
.header-content {
  float: left;
  height: $navigateHeight;
  cursor: pointer;

  .menu-icon {
    height: 100%;
    margin-left: 20px;
    color: white;
    line-height: $navigateHeight;

    > img {
      vertical-align: middle;
      width: 25px;
      height: 25px;
    }
    > svg {
      font-size: 30px;
      line-height: $navigateHeight;
      vertical-align: middle;
    }
  }
}
</style>




